import { FETCH_START, FETCH_SUCCESS, FETCH_ERROR } from "../ActionTypes";
import axios from "axios";
export const fetchStart = () => {
  return {
    type: FETCH_START
  };
};

export const fetchSuccess = () => {
  return {
    type: FETCH_SUCCESS
  };
};

export const fetchError = error => {
  return {
    type: FETCH_ERROR,
    payload: error
  };
};

export const fetchData = () => {
  return dispatch => {
    dispatch({ type: FETCH_START });
    axios
      .get(
        "https://dev132-cricket-live-scores-v1.p.rapidapi.com/matches.php?completedlimit=5&inprogresslimit=5&upcomingLimit=5",
        {
          headers: {
            "X-RapidAPI-Host": "dev132-cricket-live-scores-v1.p.rapidapi.com",
            "X-RapidAPI-Key": "729b1a5b26mshdef8ba5033acd89p1f0afcjsn7c30942bcadd"
          }
        }
      )
      .then((data) => {
        if (data.code === "OK") {
          dispatch({ type: FETCH_SUCCESS  });

          console.log("Data In Redux",data)
        } else {
          dispatch({ type: FETCH_ERROR, payload: data.error });
        }
      })
      .catch(function(error) {
        dispatch({ type: FETCH_ERROR, payload: error.message });
        console.log("Error****:", error.message);
      });
  };
};
